import React, { useState, useEffect, useRef } from 'react';
import { Line, Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

const CandlestickChart = ({ symbol, timeframe = '1m' }) => {
  const [ohlcData, setOhlcData] = useState([]);
  const [volumeData, setVolumeData] = useState([]);
  const [indicators, setIndicators] = useState({
    ema20: [],
    ema50: [],
    sma200: [],
    rsi: [],
    macd: [],
    macdSignal: [],
    macdHistogram: [],
    bollingerUpper: [],
    bollingerLower: [],
    bollingerMiddle: []
  });
  const [isLoading, setIsLoading] = useState(true);
  const intervalRef = useRef(null);

  // Generate realistic OHLC data
  const generateOHLCData = () => {
    const data = [];
    const volumeData = [];
    let basePrice = 50000;
    let baseVolume = 1000000;

    for (let i = 59; i >= 0; i--) {
      const time = new Date(Date.now() - i * 60000);
      
      // Generate realistic price movement
      const open = basePrice;
      const high = open * (1 + Math.random() * 0.02);
      const low = open * (1 - Math.random() * 0.015);
      const close = open * (1 + (Math.random() - 0.5) * 0.025);
      
      // Ensure high >= open,close and low <= open,close
      const actualHigh = Math.max(open, close, high);
      const actualLow = Math.min(open, close, low);
      
      data.push({
        time: time.toLocaleTimeString(),
        open: open,
        high: actualHigh,
        low: actualLow,
        close: close
      });

      // Generate volume
      const volume = baseVolume * (1 + (Math.random() - 0.5) * 0.5);
      volumeData.push({
        time: time.toLocaleTimeString(),
        volume: volume
      });

      // Update base price for next iteration
      basePrice = close;
      baseVolume = volume;
    }

    return { ohlc: data, volume: volumeData };
  };

  // Calculate technical indicators
  const calculateIndicators = (ohlcData) => {
    const ema20 = [];
    const ema50 = [];
    const sma200 = [];
    const rsi = [];
    const macd = [];
    const macdSignal = [];
    const macdHistogram = [];
    const bollingerUpper = [];
    const bollingerLower = [];
    const bollingerMiddle = [];

    // Calculate EMAs
    let ema20Sum = 0;
    let ema50Sum = 0;
    let sma200Sum = 0;

    ohlcData.forEach((candle, index) => {
      const close = candle.close;
      
      // EMA 20
      if (index < 20) {
        ema20Sum += close;
        ema20.push(close);
      } else {
        const ema20Value = (close * 0.095) + (ema20[index - 1] * 0.905);
        ema20.push(ema20Value);
      }

      // EMA 50
      if (index < 50) {
        ema50Sum += close;
        ema50.push(close);
      } else {
        const ema50Value = (close * 0.039) + (ema50[index - 1] * 0.961);
        ema50.push(ema50Value);
      }

      // SMA 200
      if (index < 200) {
        sma200Sum += close;
        sma200.push(close);
      } else {
        sma200Sum = sma200Sum - ohlcData[index - 200].close + close;
        sma200.push(sma200Sum / 200);
      }

      // RSI (simplified)
      if (index < 14) {
        rsi.push(50);
      } else {
        let gains = 0;
        let losses = 0;
        for (let j = index - 13; j <= index; j++) {
          const change = ohlcData[j].close - ohlcData[j - 1].close;
          if (change > 0) gains += change;
          else losses -= change;
        }
        const rs = gains / losses;
        const rsiValue = 100 - (100 / (1 + rs));
        rsi.push(rsiValue);
      }

      // MACD (simplified)
      if (index < 26) {
        macd.push(0);
        macdSignal.push(0);
        macdHistogram.push(0);
      } else {
        const ema12 = ohlcData.slice(index - 25, index + 1).reduce((sum, c) => sum + c.close, 0) / 26;
        const ema26 = ohlcData.slice(index - 25, index + 1).reduce((sum, c) => sum + c.close, 0) / 26;
        const macdValue = ema12 - ema26;
        macd.push(macdValue);
        
        if (index < 35) {
          macdSignal.push(macdValue);
          macdHistogram.push(0);
        } else {
          const signalValue = macd.slice(index - 8, index + 1).reduce((sum, val) => sum + val, 0) / 9;
          macdSignal.push(signalValue);
          macdHistogram.push(macdValue - signalValue);
        }
      }

      // Bollinger Bands
      if (index < 20) {
        bollingerMiddle.push(close);
        bollingerUpper.push(close * 1.02);
        bollingerLower.push(close * 0.98);
      } else {
        const period = 20;
        const slice = ohlcData.slice(index - period + 1, index + 1);
        const sma = slice.reduce((sum, c) => sum + c.close, 0) / period;
        const variance = slice.reduce((sum, c) => sum + Math.pow(c.close - sma, 2), 0) / period;
        const stdDev = Math.sqrt(variance);
        
        bollingerMiddle.push(sma);
        bollingerUpper.push(sma + (stdDev * 2));
        bollingerLower.push(sma - (stdDev * 2));
      }
    });

    return {
      ema20,
      ema50,
      sma200,
      rsi,
      macd,
      macdSignal,
      macdHistogram,
      bollingerUpper,
      bollingerLower,
      bollingerMiddle
    };
  };

  // Initialize data
  useEffect(() => {
    const { ohlc, volume } = generateOHLCData();
    setOhlcData(ohlc);
    setVolumeData(volume);
    
    const indicatorData = calculateIndicators(ohlc);
    setIndicators(indicatorData);
    setIsLoading(false);
  }, [symbol]);

  // Real-time updates
  useEffect(() => {
    if (isLoading) return;

    intervalRef.current = setInterval(() => {
      const { ohlc, volume } = generateOHLCData();
      
      setOhlcData(prev => {
        const newData = [...prev.slice(1), ohlc[ohlc.length - 1]];
        return newData;
      });

      setVolumeData(prev => {
        const newData = [...prev.slice(1), volume[volume.length - 1]];
        return newData;
      });

      const indicatorData = calculateIndicators([...ohlcData.slice(1), ohlc[ohlc.length - 1]]);
      setIndicators(indicatorData);
    }, 5000); // Update every 5 seconds

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isLoading, ohlcData]);

  // Prepare chart data
  const chartData = {
    labels: ohlcData.map(d => d.time),
    datasets: [
      {
        label: 'Price',
        data: ohlcData.map(d => d.close),
        borderColor: 'rgb(59, 130, 246)',
        backgroundColor: 'rgba(59, 130, 246, 0.1)',
        fill: false,
        tension: 0.1,
        pointRadius: 0,
      },
      {
        label: 'EMA 20',
        data: indicators.ema20,
        borderColor: 'rgb(168, 85, 247)',
        backgroundColor: 'transparent',
        fill: false,
        tension: 0.1,
        pointRadius: 0,
      },
      {
        label: 'EMA 50',
        data: indicators.ema50,
        borderColor: 'rgb(34, 197, 94)',
        backgroundColor: 'transparent',
        fill: false,
        tension: 0.1,
        pointRadius: 0,
      },
      {
        label: 'SMA 200',
        data: indicators.sma200,
        borderColor: 'rgb(251, 146, 60)',
        backgroundColor: 'transparent',
        fill: false,
        tension: 0.1,
        pointRadius: 0,
      },
      {
        label: 'Bollinger Upper',
        data: indicators.bollingerUpper,
        borderColor: 'rgba(156, 163, 175, 0.5)',
        backgroundColor: 'transparent',
        fill: false,
        tension: 0.1,
        pointRadius: 0,
      },
      {
        label: 'Bollinger Lower',
        data: indicators.bollingerLower,
        borderColor: 'rgba(156, 163, 175, 0.5)',
        backgroundColor: 'transparent',
        fill: false,
        tension: 0.1,
        pointRadius: 0,
      }
    ]
  };

  const volumeChartData = {
    labels: volumeData.map(d => d.time),
    datasets: [{
      label: 'Volume',
      data: volumeData.map(d => d.volume),
      backgroundColor: volumeData.map((d, i) => {
        if (i === 0) return 'rgba(34, 197, 94, 0.8)';
        const prevClose = ohlcData[i - 1]?.close || 0;
        const currentClose = ohlcData[i]?.close || 0;
        return currentClose >= prevClose ? 'rgba(34, 197, 94, 0.8)' : 'rgba(239, 68, 68, 0.8)';
      }),
      borderColor: volumeData.map((d, i) => {
        if (i === 0) return 'rgb(34, 197, 94)';
        const prevClose = ohlcData[i - 1]?.close || 0;
        const currentClose = ohlcData[i]?.close || 0;
        return currentClose >= prevClose ? 'rgb(34, 197, 94)' : 'rgb(239, 68, 68)';
      }),
      borderWidth: 1,
    }]
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    interaction: {
      mode: 'index',
      intersect: false,
    },
    scales: {
      x: {
        display: true,
        title: {
          display: true,
          text: 'Time'
        }
      },
      y: {
        type: 'linear',
        display: true,
        position: 'left',
        title: {
          display: true,
          text: 'Price ($)'
        }
      }
    },
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: `${symbol} Candlestick Chart (${timeframe})`,
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            const index = context.dataIndex;
            const ohlc = ohlcData[index];
            if (ohlc) {
              return [
                `Open: $${ohlc.open.toFixed(2)}`,
                `High: $${ohlc.high.toFixed(2)}`,
                `Low: $${ohlc.low.toFixed(2)}`,
                `Close: $${ohlc.close.toFixed(2)}`,
                `Volume: ${volumeData[index]?.volume.toLocaleString()}`
              ];
            }
            return context.dataset.label + ': ' + context.parsed.y.toFixed(2);
          }
        }
      }
    },
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Main Price Chart */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Price Chart with Indicators</h4>
        <div className="h-96">
          <Line data={chartData} options={chartOptions} />
        </div>
      </div>

      {/* Volume Chart */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Volume</h4>
        <div className="h-48">
          <Bar 
            data={volumeChartData}
            options={{
              responsive: true,
              maintainAspectRatio: false,
              scales: {
                y: {
                  beginAtZero: true,
                  title: {
                    display: true,
                    text: 'Volume'
                  }
                }
              },
              plugins: {
                legend: {
                  display: false
                }
              }
            }}
          />
        </div>
      </div>

      {/* Technical Indicators */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* RSI */}
        <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
          <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">RSI (14)</h4>
          <div className="h-48">
            <Line 
              data={{
                labels: indicators.rsi.map((_, i) => ohlcData[i]?.time || ''),
                datasets: [{
                  label: 'RSI',
                  data: indicators.rsi,
                  borderColor: 'rgb(168, 85, 247)',
                  backgroundColor: 'transparent',
                  fill: false,
                  tension: 0.1,
                  pointRadius: 0,
                }]
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                  y: {
                    min: 0,
                    max: 100,
                    grid: {
                      color: (context) => {
                        if (context.tick.value === 30 || context.tick.value === 70) {
                          return 'rgba(239, 68, 68, 0.3)';
                        }
                        return 'rgba(156, 163, 175, 0.1)';
                      }
                    }
                  }
                },
                plugins: {
                  legend: {
                    display: false
                  }
                }
              }}
            />
          </div>
        </div>

        {/* MACD */}
        <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
          <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">MACD</h4>
          <div className="h-48">
            <Line 
              data={{
                labels: indicators.macd.map((_, i) => ohlcData[i]?.time || ''),
                datasets: [
                  {
                    label: 'MACD',
                    data: indicators.macd,
                    borderColor: 'rgb(59, 130, 246)',
                    backgroundColor: 'transparent',
                    fill: false,
                    tension: 0.1,
                    pointRadius: 0,
                  },
                  {
                    label: 'Signal',
                    data: indicators.macdSignal,
                    borderColor: 'rgb(239, 68, 68)',
                    backgroundColor: 'transparent',
                    fill: false,
                    tension: 0.1,
                    pointRadius: 0,
                  }
                ]
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                  y: {
                    grid: {
                      color: 'rgba(156, 163, 175, 0.1)'
                    }
                  }
                },
                plugins: {
                  legend: {
                    display: false
                  }
                }
              }}
            />
          </div>
        </div>
      </div>

      {/* Current Values */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Current Values</h4>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">Current Price</div>
            <div className="text-lg font-semibold text-gray-900 dark:text-white">
              ${ohlcData[ohlcData.length - 1]?.close.toFixed(2) || '0.00'}
            </div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">RSI</div>
            <div className={`text-lg font-semibold ${
              (indicators.rsi[indicators.rsi.length - 1] || 0) > 70 ? 'text-red-600 dark:text-red-400' :
              (indicators.rsi[indicators.rsi.length - 1] || 0) < 30 ? 'text-green-600 dark:text-green-400' :
              'text-yellow-600 dark:text-yellow-400'
            }`}>
              {(indicators.rsi[indicators.rsi.length - 1] || 0).toFixed(1)}
            </div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">MACD</div>
            <div className={`text-lg font-semibold ${
              (indicators.macd[indicators.macd.length - 1] || 0) >= 0 ? 'text-green-600 dark:text-green-400' : 'text-red-600 dark:text-red-400'
            }`}>
              {(indicators.macd[indicators.macd.length - 1] || 0).toFixed(2)}
            </div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">Volume</div>
            <div className="text-lg font-semibold text-gray-900 dark:text-white">
              {(volumeData[volumeData.length - 1]?.volume || 0).toLocaleString()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CandlestickChart;
