import React, { useState, useEffect, useRef } from 'react';
import { Line, Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

const RealTimeChart = ({ symbol, timeframe = '1m' }) => {
  const [chartData, setChartData] = useState({
    labels: [],
    datasets: [
      {
        label: 'Price',
        data: [],
        borderColor: 'rgb(59, 130, 246)',
        backgroundColor: 'rgba(59, 130, 246, 0.1)',
        fill: true,
        tension: 0.4,
      },
      {
        label: 'Volume',
        data: [],
        borderColor: 'rgb(34, 197, 94)',
        backgroundColor: 'rgba(34, 197, 94, 0.1)',
        fill: true,
        tension: 0.4,
        yAxisID: 'volume',
      }
    ]
  });

  const [indicators, setIndicators] = useState({
    ema20: [],
    ema50: [],
    rsi: [],
    macd: [],
    bollingerUpper: [],
    bollingerLower: [],
    bollingerMiddle: []
  });

  const [currentPrice, setCurrentPrice] = useState(0);
  const [priceChange, setPriceChange] = useState(0);
  const [priceChangePercent, setPriceChangePercent] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const wsRef = useRef(null);
  const intervalRef = useRef(null);

  // Generate mock real-time data
  const generateMockData = () => {
    const now = new Date();
    const data = [];
    const volumeData = [];
    const ema20Data = [];
    const ema50Data = [];
    const rsiData = [];
    const macdData = [];
    const bbUpperData = [];
    const bbLowerData = [];
    const bbMiddleData = [];

    let basePrice = 50000; // Starting price
    let baseVolume = 1000000;

    for (let i = 59; i >= 0; i--) {
      const time = new Date(now.getTime() - i * 60000);
      
      // Generate realistic price movement
      const randomChange = (Math.random() - 0.5) * 0.02; // 1% change
      basePrice = basePrice * (1 + randomChange);
      
      // Generate volume
      const volumeChange = (Math.random() - 0.5) * 0.3;
      baseVolume = baseVolume * (1 + volumeChange);
      
      data.push({
        x: time.toLocaleTimeString(),
        y: basePrice
      });
      
      volumeData.push({
        x: time.toLocaleTimeString(),
        y: baseVolume
      });

      // Calculate indicators
      const ema20 = basePrice * (1 + (Math.random() - 0.5) * 0.01);
      const ema50 = basePrice * (1 + (Math.random() - 0.5) * 0.015);
      const rsi = 30 + Math.random() * 40; // RSI between 30-70
      const macd = (Math.random() - 0.5) * 100;
      
      ema20Data.push({ x: time.toLocaleTimeString(), y: ema20 });
      ema50Data.push({ x: time.toLocaleTimeString(), y: ema50 });
      rsiData.push({ x: time.toLocaleTimeString(), y: rsi });
      macdData.push({ x: time.toLocaleTimeString(), y: macd });
      
      // Bollinger Bands
      const bbMultiplier = 2;
      const bbUpper = basePrice * (1 + bbMultiplier * 0.01);
      const bbLower = basePrice * (1 - bbMultiplier * 0.01);
      const bbMiddle = basePrice;
      
      bbUpperData.push({ x: time.toLocaleTimeString(), y: bbUpper });
      bbLowerData.push({ x: time.toLocaleTimeString(), y: bbLower });
      bbMiddleData.push({ x: time.toLocaleTimeString(), y: bbMiddle });
    }

    return {
      priceData: data,
      volumeData: volumeData,
      ema20Data,
      ema50Data,
      rsiData,
      macdData,
      bbUpperData,
      bbLowerData,
      bbMiddleData
    };
  };

  // Initialize chart data
  useEffect(() => {
    const mockData = generateMockData();
    
    setChartData(prev => ({
      ...prev,
      labels: mockData.priceData.map(d => d.x),
      datasets: [
        {
          ...prev.datasets[0],
          data: mockData.priceData.map(d => d.y),
        },
        {
          ...prev.datasets[1],
          data: mockData.volumeData.map(d => d.y),
        }
      ]
    }));

    setIndicators({
      ema20: mockData.ema20Data,
      ema50: mockData.ema50Data,
      rsi: mockData.rsiData,
      macd: mockData.macdData,
      bollingerUpper: mockData.bbUpperData,
      bollingerLower: mockData.bbLowerData,
      bollingerMiddle: mockData.bbMiddleData
    });

    setCurrentPrice(mockData.priceData[mockData.priceData.length - 1].y);
    setIsLoading(false);
  }, [symbol]);

  // Real-time updates
  useEffect(() => {
    if (isLoading) return;

    intervalRef.current = setInterval(() => {
      const mockData = generateMockData();
      const latestPrice = mockData.priceData[mockData.priceData.length - 1].y;
      
      setCurrentPrice(latestPrice);
      setPriceChange(latestPrice - 50000); // Compare with base price
      setPriceChangePercent(((latestPrice - 50000) / 50000) * 100);

      // Update chart data with new point
      setChartData(prev => {
        const newLabels = [...prev.labels.slice(1), mockData.priceData[mockData.priceData.length - 1].x];
        const newPriceData = [...prev.datasets[0].data.slice(1), latestPrice];
        const newVolumeData = [...prev.datasets[1].data.slice(1), mockData.volumeData[mockData.volumeData.length - 1].y];

        return {
          ...prev,
          labels: newLabels,
          datasets: [
            {
              ...prev.datasets[0],
              data: newPriceData,
            },
            {
              ...prev.datasets[1],
              data: newVolumeData,
            }
          ]
        };
      });

      // Update indicators
      setIndicators(prev => ({
        ema20: [...prev.ema20.slice(1), mockData.ema20Data[mockData.ema20Data.length - 1]],
        ema50: [...prev.ema50.slice(1), mockData.ema50Data[mockData.ema50Data.length - 1]],
        rsi: [...prev.rsi.slice(1), mockData.rsiData[mockData.rsiData.length - 1]],
        macd: [...prev.macd.slice(1), mockData.macdData[mockData.macdData.length - 1]],
        bollingerUpper: [...prev.bollingerUpper.slice(1), mockData.bbUpperData[mockData.bbUpperData.length - 1]],
        bollingerLower: [...prev.bollingerLower.slice(1), mockData.bbLowerData[mockData.bbLowerData.length - 1]],
        bollingerMiddle: [...prev.bollingerMiddle.slice(1), mockData.bbMiddleData[mockData.bbMiddleData.length - 1]]
      }));
    }, 1000); // Update every second

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isLoading]);

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    interaction: {
      mode: 'index',
      intersect: false,
    },
    scales: {
      x: {
        display: true,
        title: {
          display: true,
          text: 'Time'
        }
      },
      y: {
        type: 'linear',
        display: true,
        position: 'left',
        title: {
          display: true,
          text: 'Price ($)'
        }
      },
      volume: {
        type: 'linear',
        display: true,
        position: 'right',
        title: {
          display: true,
          text: 'Volume'
        },
        grid: {
          drawOnChartArea: false,
        },
      },
    },
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: `${symbol} Real-Time Chart (${timeframe})`,
      },
    },
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {/* Price Header */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
        <div className="flex items-center justify-between">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white">{symbol}</h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">Real-time {timeframe} chart</p>
          </div>
          <div className="text-right">
            <div className="text-2xl font-bold text-gray-900 dark:text-white">
              ${currentPrice.toLocaleString()}
            </div>
            <div className={`text-sm font-medium ${
              priceChange >= 0 ? 'text-green-600 dark:text-green-400' : 'text-red-600 dark:text-red-400'
            }`}>
              {priceChange >= 0 ? '+' : ''}{priceChange.toFixed(2)} ({priceChangePercent.toFixed(2)}%)
            </div>
          </div>
        </div>
      </div>

      {/* Main Chart */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
        <div className="h-96">
          <Line data={chartData} options={chartOptions} />
        </div>
      </div>

      {/* Technical Indicators */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* RSI Chart */}
        <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
          <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">RSI (14)</h4>
          <div className="h-48">
            <Line 
              data={{
                labels: indicators.rsi.map(d => d.x),
                datasets: [{
                  label: 'RSI',
                  data: indicators.rsi.map(d => d.y),
                  borderColor: 'rgb(168, 85, 247)',
                  backgroundColor: 'rgba(168, 85, 247, 0.1)',
                  fill: false,
                  tension: 0.4,
                }]
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                  y: {
                    min: 0,
                    max: 100,
                    grid: {
                      color: (context) => {
                        if (context.tick.value === 30 || context.tick.value === 70) {
                          return 'rgba(239, 68, 68, 0.3)';
                        }
                        return 'rgba(156, 163, 175, 0.1)';
                      }
                    }
                  }
                },
                plugins: {
                  legend: {
                    display: false
                  }
                }
              }}
            />
          </div>
        </div>

        {/* MACD Chart */}
        <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
          <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">MACD</h4>
          <div className="h-48">
            <Bar 
              data={{
                labels: indicators.macd.map(d => d.x),
                datasets: [{
                  label: 'MACD',
                  data: indicators.macd.map(d => d.y),
                  backgroundColor: indicators.macd.map(d => d.y >= 0 ? 'rgba(34, 197, 94, 0.8)' : 'rgba(239, 68, 68, 0.8)'),
                  borderColor: indicators.macd.map(d => d.y >= 0 ? 'rgb(34, 197, 94)' : 'rgb(239, 68, 68)'),
                  borderWidth: 1,
                }]
              }}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                  y: {
                    grid: {
                      color: 'rgba(156, 163, 175, 0.1)'
                    }
                  }
                },
                plugins: {
                  legend: {
                    display: false
                  }
                }
              }}
            />
          </div>
        </div>
      </div>

      {/* Indicator Values */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-4 shadow-sm">
        <h4 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Technical Indicators</h4>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">EMA 20</div>
            <div className="text-lg font-semibold text-blue-600 dark:text-blue-400">
              ${indicators.ema20[indicators.ema20.length - 1]?.y.toFixed(2) || '0.00'}
            </div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">EMA 50</div>
            <div className="text-lg font-semibold text-purple-600 dark:text-purple-400">
              ${indicators.ema50[indicators.ema50.length - 1]?.y.toFixed(2) || '0.00'}
            </div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">RSI</div>
            <div className={`text-lg font-semibold ${
              (indicators.rsi[indicators.rsi.length - 1]?.y || 0) > 70 ? 'text-red-600 dark:text-red-400' :
              (indicators.rsi[indicators.rsi.length - 1]?.y || 0) < 30 ? 'text-green-600 dark:text-green-400' :
              'text-yellow-600 dark:text-yellow-400'
            }`}>
              {(indicators.rsi[indicators.rsi.length - 1]?.y || 0).toFixed(1)}
            </div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-500 dark:text-gray-400">MACD</div>
            <div className={`text-lg font-semibold ${
              (indicators.macd[indicators.macd.length - 1]?.y || 0) >= 0 ? 'text-green-600 dark:text-green-400' : 'text-red-600 dark:text-red-400'
            }`}>
              {(indicators.macd[indicators.macd.length - 1]?.y || 0).toFixed(2)}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RealTimeChart;

