import React, { useState } from 'react';
import RealTimeChart from '../components/Charts/RealTimeChart';
import CandlestickChart from '../components/Charts/CandlestickChart';
import { BarChart3, Clock, TrendingUp, Activity, BarChart, LineChart } from 'lucide-react';

const Charts = () => {
  const [selectedTimeframe, setSelectedTimeframe] = useState('1m');
  const [selectedPair, setSelectedPair] = useState('BTC/USDT');
  const [chartType, setChartType] = useState('candlestick'); // 'candlestick' or 'line'

  const timeframes = [
    { value: '1m', label: '1 Minute', icon: Clock },
    { value: '5m', label: '5 Minutes', icon: Clock },
    { value: '15m', label: '15 Minutes', icon: Clock },
    { value: '1h', label: '1 Hour', icon: Clock },
    { value: '4h', label: '4 Hours', icon: Clock },
    { value: '1d', label: '1 Day', icon: Clock },
  ];

  const tradingPairs = [
    { symbol: 'BTC/USDT', name: 'Bitcoin', type: 'crypto', color: 'text-orange-500' },
    { symbol: 'ETH/USDT', name: 'Ethereum', type: 'crypto', color: 'text-blue-500' },
    { symbol: 'BNB/USDT', name: 'Binance Coin', type: 'crypto', color: 'text-yellow-500' },
    { symbol: 'ADA/USDT', name: 'Cardano', type: 'crypto', color: 'text-blue-600' },
    { symbol: 'EUR/USD', name: 'Euro/US Dollar', type: 'forex', color: 'text-green-500' },
    { symbol: 'GBP/USD', name: 'Pound/US Dollar', type: 'forex', color: 'text-purple-500' },
    { symbol: 'USD/JPY', name: 'US Dollar/Yen', type: 'forex', color: 'text-red-500' },
    { symbol: 'USD/CHF', name: 'US Dollar/Franc', type: 'forex', color: 'text-indigo-500' },
  ];

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <div className="flex items-center space-x-3 mb-4">
            <BarChart3 className="h-8 w-8 text-blue-600 dark:text-blue-400" />
            <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Real-Time Charts</h1>
          </div>
          <p className="text-gray-600 dark:text-gray-400">
            Advanced technical analysis with real-time price data, indicators, and charting tools
          </p>
        </div>

        {/* Controls */}
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6 mb-8">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {/* Trading Pair Selection */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                Select Trading Pair
              </label>
              <div className="grid grid-cols-2 gap-2">
                {tradingPairs.map((pair) => (
                  <button
                    key={pair.symbol}
                    onClick={() => setSelectedPair(pair.symbol)}
                    className={`p-3 rounded-lg border-2 transition-all duration-200 ${
                      selectedPair === pair.symbol
                        ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                        : 'border-gray-200 dark:border-gray-600 hover:border-gray-300 dark:hover:border-gray-500'
                    }`}
                  >
                    <div className="text-left">
                      <div className={`font-medium ${pair.color}`}>{pair.symbol}</div>
                      <div className="text-xs text-gray-500 dark:text-gray-400">{pair.name}</div>
                      <div className="text-xs text-gray-400 dark:text-gray-500 capitalize">{pair.type}</div>
                    </div>
                  </button>
                ))}
              </div>
            </div>

            {/* Timeframe Selection */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                Select Timeframe
              </label>
              <div className="grid grid-cols-3 gap-2">
                {timeframes.map((tf) => {
                  const Icon = tf.icon;
                  return (
                    <button
                      key={tf.value}
                      onClick={() => setSelectedTimeframe(tf.value)}
                      className={`p-3 rounded-lg border-2 transition-all duration-200 flex flex-col items-center space-y-1 ${
                        selectedTimeframe === tf.value
                          ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                          : 'border-gray-200 dark:border-gray-600 hover:border-gray-300 dark:hover:border-gray-500'
                      }`}
                    >
                      <Icon className="h-4 w-4 text-gray-600 dark:text-gray-400" />
                      <span className="text-xs font-medium text-gray-700 dark:text-gray-300">
                        {tf.label}
                      </span>
                    </button>
                  );
                })}
              </div>
            </div>

            {/* Chart Type Selection */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                Chart Type
              </label>
              <div className="grid grid-cols-2 gap-2">
                <button
                  onClick={() => setChartType('candlestick')}
                  className={`p-3 rounded-lg border-2 transition-all duration-200 flex flex-col items-center space-y-1 ${
                    chartType === 'candlestick'
                      ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                      : 'border-gray-200 dark:border-gray-600 hover:border-gray-300 dark:hover:border-gray-500'
                  }`}
                >
                  <BarChart className="h-4 w-4 text-gray-600 dark:text-gray-400" />
                  <span className="text-xs font-medium text-gray-700 dark:text-gray-300">Candlestick</span>
                </button>
                <button
                  onClick={() => setChartType('line')}
                  className={`p-3 rounded-lg border-2 transition-all duration-200 flex flex-col items-center space-y-1 ${
                    chartType === 'line'
                      ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                      : 'border-gray-200 dark:border-gray-600 hover:border-gray-300 dark:hover:border-gray-500'
                  }`}
                >
                  <LineChart className="h-4 w-4 text-gray-600 dark:text-gray-400" />
                  <span className="text-xs font-medium text-gray-700 dark:text-gray-300">Line</span>
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Chart Display */}
        <div className="space-y-8">
          {/* Main Chart */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm overflow-hidden">
            <div className="p-6 border-b border-gray-200 dark:border-gray-700">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
                    {selectedPair} - {selectedTimeframe} Chart
                  </h2>
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    Real-time price data with technical indicators
                  </p>
                </div>
                <div className="flex items-center space-x-2">
                  <div className="flex items-center space-x-1 text-sm text-gray-500 dark:text-gray-400">
                    <Activity className="h-4 w-4" />
                    <span>Live</span>
                  </div>
                  <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
                </div>
              </div>
            </div>
            <div className="p-6">
              {chartType === 'candlestick' ? (
                <CandlestickChart symbol={selectedPair} timeframe={selectedTimeframe} />
              ) : (
                <RealTimeChart symbol={selectedPair} timeframe={selectedTimeframe} />
              )}
            </div>
          </div>

          {/* Additional Charts Grid */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* Volume Analysis */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Volume Analysis
              </h3>
              <div className="space-y-4">
                <div className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                  <span className="text-sm text-gray-600 dark:text-gray-400">24h Volume</span>
                  <span className="font-medium text-gray-900 dark:text-white">$2.5B</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Volume Change</span>
                  <span className="font-medium text-green-600 dark:text-green-400">+12.5%</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Avg Volume</span>
                  <span className="font-medium text-gray-900 dark:text-white">$2.1B</span>
                </div>
              </div>
            </div>

            {/* Market Sentiment */}
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Market Sentiment
              </h3>
              <div className="space-y-4">
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Bullish</span>
                  <div className="flex items-center space-x-2">
                    <div className="w-24 bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                      <div className="bg-green-500 h-2 rounded-full" style={{ width: '65%' }}></div>
                    </div>
                    <span className="text-sm font-medium text-gray-900 dark:text-white">65%</span>
                  </div>
                </div>
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Neutral</span>
                  <div className="flex items-center space-x-2">
                    <div className="w-24 bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                      <div className="bg-yellow-500 h-2 rounded-full" style={{ width: '25%' }}></div>
                    </div>
                    <span className="text-sm font-medium text-gray-900 dark:text-white">25%</span>
                  </div>
                </div>
                <div className="flex items-center justify-between">
                  <span className="text-sm text-gray-600 dark:text-gray-400">Bearish</span>
                  <div className="flex items-center space-x-2">
                    <div className="w-24 bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                      <div className="bg-red-500 h-2 rounded-full" style={{ width: '10%' }}></div>
                    </div>
                    <span className="text-sm font-medium text-gray-900 dark:text-white">10%</span>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Quick Stats */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6">
              <div className="flex items-center">
                <div className="p-2 bg-blue-100 dark:bg-blue-900/20 rounded-lg">
                  <TrendingUp className="h-6 w-6 text-blue-600 dark:text-blue-400" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">24h High</p>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">$51,250</p>
                </div>
              </div>
            </div>

            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6">
              <div className="flex items-center">
                <div className="p-2 bg-red-100 dark:bg-red-900/20 rounded-lg">
                  <TrendingUp className="h-6 w-6 text-red-600 dark:text-red-400 transform rotate-180" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">24h Low</p>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">$49,800</p>
                </div>
              </div>
            </div>

            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6">
              <div className="flex items-center">
                <div className="p-2 bg-green-100 dark:bg-green-900/20 rounded-lg">
                  <Activity className="h-6 w-6 text-green-600 dark:text-green-400" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Volatility</p>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">2.8%</p>
                </div>
              </div>
            </div>

            <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm p-6">
              <div className="flex items-center">
                <div className="p-2 bg-purple-100 dark:bg-purple-900/20 rounded-lg">
                  <BarChart3 className="h-6 w-6 text-purple-600 dark:text-purple-400" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Market Cap</p>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">$1.2T</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Charts;
